.header{
    padding: 10px;
    background-color: #3C5EA7;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #F9F3F3;
    font-family: Roboto, sans-serif, Arial, Helvetica;
}

.header h3{
    flex: 1;
    min-width: 170px;
    font-weight: 500;
    text-align: start;
}

.hamburger{
    display: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}

.hamburger-menu{
    position: absolute;
    right: 5px;
    top: 60px;
    display: none;
    background-color: #F4F6F9;
    transition: all 0.8s ease-in-out;
    opacity: 0.8;
}

.hamburger-menu.active{
    display: block;
}

.hamburger:hover{
    opacity: 0.5;
    transform: scale(1.2);
}

.hamburger-list{
    padding: 8px 25px 8px 25px;
    line-height: 26px;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
}

@media (max-width: 700px) {
    .navbar {
        display: none;
    }
    .hamburger {
        display: block;
    }
}

.navbar-list{
    display: flex;
    align-items: end;
    list-style: none;
}

.navbar-list li a{
    margin-right: 10px;
    color: #F9F3F3;
    transition: all .2s ease-in-out;
}

.navbar-list li a:hover{
    color: rgba(255, 255, 255, 0.5);
}